//@ts-nocheck
import {
  selectFeatureFlagConfigData,
  selectUserRole,
} from 'Authentication/slice/selectors';
import React from 'react';
import { useSelector } from 'react-redux';
import TabsComponent from '../../../app/components/TabsComponent';
import { AssignDevices } from '../AssignDevices/Loadable';
import { Calender } from '../Calender/Loadable';
import { DeviceHub } from '../DeviceHub/Loadable';
import { MozarkDevices } from '../MozarkDevices/Loadable';
import { MyDevices } from '../MyDevices/Loadable';
import { useOutletContext } from 'react-router-dom';
//import features from '../../../features.json';
export function DeviceCloud() {
  const features = useSelector(selectFeatureFlagConfigData);
  const userRole = useSelector(selectUserRole);
  // const [isAuthenticated, userRole] = useOutletContext();
  const listofTabs =
    userRole === 'admin'
      ? [
          'My Devices',
          features.features.enable['features-schedule-calendar'] && 'Calendar',
          // //'Device Hub',
          // 'Assign Devices',
        ]
      : [
          'My Devices',
          features.features.enable['features-schedule-calendar'] && 'Calendar',
          // 'Mozark Devices',
          //'Device Hub',
          // 'Assign Devices',
        ];
  const listofComponents =
    userRole === 'admin'
      ? [
          <MyDevices></MyDevices>,
          // <MozarkDevices></MozarkDevices>,
          //  <DeviceHub></DeviceHub>,
          // <AssignDevices></AssignDevices>,
          features.features.enable['features-schedule-calendar'] && (
            <Calender></Calender>
          ),
        ]
      : [
          <MyDevices></MyDevices>,
          // <MozarkDevices></MozarkDevices>,
          //  <DeviceHub></DeviceHub>,
          //  <AssignDevices></AssignDevices>,
          features.features.enable['features-schedule-calendar'] && (
            <Calender></Calender>
          ),
        ];

  return (
    <>
      <TabsComponent
        isLazy={true}
        isSubContainer={true}
        listofTabs={listofTabs}
        listofComponents={listofComponents}
      ></TabsComponent>
    </>
  );
}

export default DeviceCloud;
